var _self;
export default {
	data() {
		return {
			from: '',
			mobile: '',
			password: '',
			windowHeight: '', //设备高度
			hasFixed: true // 是否显示底部定位元素
		};
	},
	onLoad(options) {
		_self = this;
		_self.from = _self.$Route.query.from;
		// 获取设备高度，解决flex定位顶起的问题
		uni.getSystemInfo({
			success: res => {
				this.windowHeight = res.windowHeight;
			}
		});
		uni.onWindowResize(res => {
			if (res.size.windowHeight < this.windowHeight) {
				this.hasFixed = false;
			} else {
				this.hasFixed = true;
			}
		});
	},
	onBackPress() {},
	methods: {
		async doLogin() {
			uni.hideKeyboard();
			// if(!this.$common.checkPhone(this.mobile)){
			// 	return false;
			// }
			// if(!this.$common.checkPassword(this.password)){
			// 	return false;
			// }
			if(!_self.mobile){
				uni.showToast({
					icon:"none",
					title:"请输入用户名"
				})
				return false;
			}
			if(!_self.password){
				uni.showToast({
					icon:"none",
					title:"请输入密码"
				})
				return false;
			}
			
			console.log(_self.$req.defaultReq.baseData);
			console.log(_self.$req.defaultReq.header);
			_self.$ajax({
				path: _self.$api.login,
				data: {
					account: _self.mobile,
					password: _self.password
				},
				success(res){
					let { data, code, msg } = res.data;
					uni.showToast({
						title: msg,
						icon: code ? 'success' : 'none',
						success() {
							setTimeout(function() {
								if (code) {
									uni.setStorageSync('USERINFO', data);
									uni.hideToast()
									_self.$Router.replaceAll({ name: "index", params: {} });
								} else {
									
								}
							}, 1200);
						}
					});
				}
			})
		},
		fixedToggle(has) {
			//是否显示三方登录
			this.tabbar = has || false;
		}
	}
};
